import React from "react";
import Switch from "antd/es/switch";
import Tooltip from "antd/es/tooltip";

export default class Home extends React.Component {
  state = {
    checkState: window.localStorage.getItem("preload") !== "false",
    disable: !window.Proxy || !window.CustomElementRegistry,
    degradeState: window.localStorage.getItem("degrade") === "true" || !window.Proxy || !window.CustomElementRegistry,
  };
  checkStateChange = (check) => {
    window.localStorage.setItem("preload", check);
    setTimeout(() => window.location.reload(), 1000);
  };
  degradeStateChange = (check) => {
    window.localStorage.setItem("degrade", check);
    setTimeout(() => window.location.reload(), 1000);
  };
  render() {
    return (
      <div className="home">
        <div className="fixed">
          <a href="https://wujie.pages.woa.com/" target="_blank" className="docs" rel="noreferrer">
            
          </a>
          <a
            href="https://git.woa.com/wujie/wujie/tree/master/examples/main-react/"
            target="_blank"
            className="docs"
            rel="noreferrer"
          >
            
          </a>
          <Tooltip title="history">
            <a href="https://wujie-demo.pages.woa.com/" target="_blank" className="docs" rel="noreferrer">
              vue
            </a>
          </Tooltip>
          <Tooltip title="+">
            <Switch
              className="switch"
              checkedChildren=""
              unCheckedChildren=""
              defaultChecked={this.state.checkState}
              onChange={this.checkStateChange}
            ></Switch>
          </Tooltip>
          <Tooltip title="shadow+proxy">
            <Switch
              className="switch"
              checkedChildren=""
              unCheckedChildren=""
              disabled={this.state.disable}
              defaultChecked={this.state.degradeState}
              onChange={this.degradeStateChange}
            ></Switch>
          </Tooltip>
        </div>
        <h1 className="header">
          <img
            alt=""
            style={{ width: "70px", height: "70px", marginRight: "15px" }}
            src="https://vfiles.gtimg.cn/wuji_dashboard/xy/test_wuji_damy/86gA3vf6.svg"
          />
          <span className="bland"></span>
        </h1>
        <h2 className="subtitle"></h2>

        <div className="detail-content">
          <div className="item">
            <div className="title"> </div>
            <div className="detail">
              <ul>
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </div>
          </div>
          <div className="item">
            <div className="title"> </div>
            <div className="detail">
              <ul>
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </div>
          </div>
          <div className="item">
            <div className="title"> </div>
            <div className="detail">
              <ul>
                <li></li>
                <li>API</li>
                <li></li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
